export default function QuestionChoice(props) {

    console.log('QuestionChoice - props.info', props.info)
    return (
        <div className="page-section q-wrapper">
            <div className="check-group">
                {
                    props.info.map( item => {
                        return <button key={item.choiceId} className="btn-q">{item.contents}</button>
                    })
                }
            </div>
        </div>
    )
}