import { PageLayout, PageButtons } from "@chac/common/style/sytles"
import { PageMedicalQuestion  } from "../style/styles";
import { useState, useEffect } from 'react';
import QuestionTitle from "../components/QuestionTitle";
import QuestionChoice from "../components/QuestionChoice";
import { useAnimate } from "framer-motion";
import { useGetMedicalQuestionQuery } from "../query/dummy";

export default function MedicalQuestion() {
	
  // 반려동물 타입: DOG | CAT
  const [ petType, setPetType ] = useState('DOG');

  // 더미데이터 가져오기
  const {data: dummy, isFetching} = useGetMedicalQuestionQuery(petType);
  
  // 현제 문진페이지
  const [ curCategoryNum, setCurCategoryNum ] = useState(1);

  // 전체 문진페이지 수
  const [ totalCategoryNum, settTotalCategoryNum ] = useState(10);

  
  let containerPox = 0;
  const [scope, animate] = useAnimate()

  const [screenWidth, setScreenWidth] = useState(0);

  useEffect(() => {

    setScreenWidth( window.innerWidth)
    
    return () => {
      
    }
  }, [])
  

  const btnPrevHandler = (e) => {
    if ( containerPox === 0 ) return;
    containerPox += screenWidth;
    animate(scope.current, { x: containerPox }, { duration: 0.5 })
  }

  const btnNextHandler = (e) => {    
    containerPox -= screenWidth;
    animate(scope.current, { x: containerPox }, { duration: 0.5 })
  }

//   {
//     "categoryId": 3,
//     "categoryType": "치아",
//     "petType": "DOG",
//     "title": "#NAME(이)가 자주 보이는 행동이나 습관을 모두 선택해주세요.",
//     "comment": "치석은 입냄새 뿐만 아니라 잇몸의 염증을 유발해요.",
//     "number": 2,
//     "multiFg": "Y"
// },

// {
//   "categoryId": 2,
//   "choiceId": 4,
//   "contents": "발 끝을 수시로 핥거나 깨물어요."
// },

  const getChoiceInfo = inId => {
    console.log('inId', inId)
    return dummy.choice.filter( item => {
      if ( item.categoryId == inId ) return item;
    })
  }
  // 데이터 로딩중일때
  if (isFetching) {
	return <h1> 데이터 로딩중...</h1>
  }

  return (
	  <PageLayout>
        <PageMedicalQuestion sWidth={screenWidth}>
            <div className="question-move-wrapper" ref={scope}>
            {
                dummy.category.map( (item, index) => {
                const choiceData = getChoiceInfo(item.categoryId);
                const keyId = item.categoryId;
                // console.log('choiceData', choiceData)
                return <div className="question-item-wrap" key={keyId}>
                    <QuestionTitle key={index} title={item.title} comment={item.comment} />
                    <QuestionChoice key={index+1} info={choiceData} />
                </div>
                })
            }
            </div>
            <PageButtons>
                <button className="btn btn-prev" onClick={btnPrevHandler}>이전</button>
                <button className="btn btn-next" onClick={btnNextHandler}>다음</button>
            </PageButtons>
        </PageMedicalQuestion>
        </PageLayout>
  )
}
